








import { Component, Prop, Vue } from 'vue-property-decorator';
import { IAuthors } from '@edave64/dddg-repo-filters/dist/authors';
import { IPack } from '@edave64/dddg-repo-filters/dist/pack';
import SingleBox from './layouts/SingleBox.vue';

@Component({ components: { SingleBox } })
export default class Dialog extends Vue {
	public focus() {
		(this.$refs.packDialog as SingleBox).focus();
	}

	public setSearch(search: string) {
		(this.$refs.packDialog as SingleBox).setSearch(search);
	}

	public leave(): void {
		this.setSearch('');
		this.$emit('leave');
	}
}
